@use './variables' as *;

@mixin mobile {
	@media only screen and (max-width: $mobile-width) {
		@content;
	}
}

@mixin tablet {
	@media only screen and (max-width: $tablet-width) {
		@content;
	}
}

@mixin medium-tablet {
	@media only screen and (max-width: $medium-tablet-width) {
		@content;
	}
}
